.page-wrapper {
  padding-top: 80px;
  display: block;
}
@keyframes fadeInUpTop {
  0% {
    opacity: 0;
    transform: translate3d(30, 50%, 0);
  }
  to {
    opacity: 1;
    transform: translateZ(0);
  }
}
.page-wrapper {
  background: #f9fbfd;
  /* position: relative; */
  animation: fadeInUpTop 1s;
  /* display: none; */
  /* box-shadow: 0 3px 9px 0 rgba(162,176,190,.15); */
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  height: 100%;
}

#main-wrapper[data-layout="vertical"][data-sidebartype="full"] .page-wrapper {
  margin-left: 260px;
}

.page-wrapper > .page-breadcrumb {
  max-width: 1300px;
  margin: 0 auto;
}

.page-breadcrumb {
  padding: 30px 35px 0;
}

.row {
  --bs-gutter-x: 30px;
  --bs-gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(-1 * var(--bs-gutter-y));
  margin-right: calc(-0.5 * var(--bs-gutter-x));
  margin-left: calc(-0.5 * var(--bs-gutter-x));
}

.align-self-center {
  align-self: center !important;
}

.col-7 {
  flex: 0 0 auto;
  width: 58.33333333%;
}

.page-breadcrumb .breadcrumb {
  background: 0 0;
  font-size: 0.875rem;
}

.p-0 {
  padding: 0 !important;
}

.m-0 {
  margin: 0 !important;
}

html body .font-weight-medium {
  font-weight: 900;
  font-size: 18px;
}

.text-dark {
  --bs-text-opacity: 1;
  color: rgba(var(--bs-dark-rgb), var(--bs-text-opacity)) !important;
}

.mb-1 {
  margin-bottom: 0.25rem !important;
}

.page-wrapper > .container-fluid,
.page-wrapper > .container-lg,
.page-wrapper > .container-md,
.page-wrapper > .container-sm,
.page-wrapper > .container-xl,
.page-wrapper > .container-xxl {
  padding: 35px;
  max-width: 1300px;
  min-height: calc(100vh - 210px);
}

.container,
.container-fluid,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
  --bs-gutter-x: 30px;
  --bs-gutter-y: 0;
  width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-right: auto;
  margin-left: auto;
}

.col-12 {
  flex: 0 0 auto;
  width: 100%;
}

.row > * {
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: calc(var(--bs-gutter-x) * 0.5);
  padding-left: calc(var(--bs-gutter-x) * 0.5);
  margin-top: var(--bs-gutter-y);
}

.card,
.card-group {
  margin-bottom: 30px;
}

.card {
  --bs-card-spacer-y: 25px;
  --bs-card-spacer-x: 25px;
  --bs-card-title-spacer-y: 0.5rem;
  --bs-card-border-width: 0;
  --bs-card-border-color: #e9ecef;
  --bs-card-border-radius: 0.25rem;
  --bs-card-inner-border-radius: 0.25rem;
  --bs-card-cap-padding-y: 12.5px;
  --bs-card-cap-padding-x: 25px;
  --bs-card-cap-bg: rgba(var(--bs-body-color-rgb), 0.03);
  --bs-card-bg: #fff;
  --bs-card-img-overlay-padding: 1rem;
  --bs-card-group-margin: 15px;
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  height: var(--bs-card-height);
  word-wrap: break-word;
  background-color: var(--bs-card-bg);
  background-clip: border-box;
  border: var(--bs-card-border-width) solid var(--bs-card-border-color);
  border-radius: var(--bs-card-border-radius);
  box-shadow: var(--bs-card-box-shadow);
}

.card-body {
  flex: 1 1 auto;
  padding: 25px 60px;
  color: var(--bs-card-color);
}

